.basicLightbox {
  position: fixed;
  display: flex;
  justify-content: center;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  overflow-x: auto;

  background: rgba(102, 90, 90, 0.8);
  opacity: 0.01;
  transition: opacity 0.4s ease;
  z-index: 1000;
  will-change: opacity;

  transition: opacity 1000ms var(--cubic), visibility 500ms var(--cubic);

  &--visible {
    opacity: 1;
  }

  &__placeholder {
    height: fit-content;
    margin: auto;
  }

  & .team__modal {
    text-decoration: underline;
    cursor: pointer;
  }
}

.dialog {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 15px;
  margin-bottom: 15px;

  background-color: $main-bg-color;
  border-radius: 15px;

  width: 240px;

  @media screen and (min-width: $tablet) {
    width: 400px;
    border-radius: 25px;
  }

  @media screen and (min-width: $desktop) {
    width: 900px;
    height: 670px;
    padding: 25px;
  }
}

.team__wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  border-bottom-left-radius: 15px;
  border-bottom-right-radius: 15px;

  @media (min-width: $tablet) {
    border-bottom-left-radius: 25px;
    border-bottom-right-radius: 25px;
  }
}
.team__lead {
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: calc(100% / 1);
}

.team__card {
  position: relative;

  display: flex;
  flex-direction: column;
  align-items: center;

  margin: 10px 10px;

  width: calc((100% - 80px) / 1);

  @media screen and (min-width: $tablet) {
    width: calc((100% - 160px) / 2);
  }

  @media screen and (min-width: $desktop) {
    margin: 15px 10px;

    width: calc((100% - 240px) / 4);
  }
}

.team__image {
  margin-bottom: 10px;
  box-shadow: 0px -2px 32px 1px rgba(224, 115, 25, 0.69);
  object-fit: cover;

  height: 90px;
  width: 90px;

  &:hover {
    box-shadow: 0px -2px 32px 1px $accent-color;
    transform: scale(1.2);
  }
  border-radius: 50%;
}

.team__name {
  margin-bottom: 5px;
  text-align: center;

  font-family: $main-font;
  font-weight: 700;
}

.team__role {
  font-family: $main-font;
  text-align: center;
  margin-bottom: 10px;

  @media screen and (max-width: ($tablet - 1)) {
    font-size: 14px;
  }

  color: var(--accent-color);
}
.team__modal {
  text-decoration: underline;
  cursor: pointer;
}
.team__social {
  display: flex;
  :not(:first-child) {
    margin-left: 7px;
  }
}

.team__icon {
  transform: scale(1);

  &:hover,
  &:focus {
    transform: scale(1.5);
  }
}

.title-wrapper {
  margin-top: 20px;
  margin-bottom: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: calc(100% / 1);
  @media screen and (min-width: $tablet) and (max-width: ($tablet - 1)) {
    margin-bottom: 30px;
    margin-top: 20px;
  }
  @media screen and (min-width: $desktop) {
    margin-top: 20px;
    margin-bottom: 10px;
  }
}

.logo {
  height: 24px;
  width: 65px;
  margin-right: 10px;
  @media screen and (min-width: $tablet) and (max-width: ($tablet - 1)) {
    height: 23px;
    width: 65px;
    margin-right: 15px;
  }
  @media screen and (min-width: $desktop) {
    height: 24px;
    width: 80px;
    margin-right: 15px;
  }
}

.logo__icon {
  fill: #000;

  width: 16px;
  height: 16px;
}

.logo__icon-linkedin {
  fill: rgba(66, 114, 247, 0.993);
}
